import { defineStore } from 'pinia'

export const useThaiStore = defineStore('ThaiStore', {
    state: () => ({
        cart: []
    }),
    getters: {
        listProducts(state) {
            return state.products;
        }
    },
    actions: {
        addToCart(product) {
            this.cart.push({product, id: this.id++})
        },
        deleteCart(prodId) {
            this.cart = this.cart.filter((object) => {
                return object.id !== prodId
            })
        }
    }
})